.nav{
    margin: 55px;
    display: flex;
    flex-direction: row;
}

.nav a {
    text-decoration: none;
}

.nav h3 {
    margin-right: 50px;
}

.nav ul {
    font-size: 35px;
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.nav ul li {
    margin-left: 50px;
}

ul button {
    margin-left: 15px;
    height: 40px;
    margin-top: 7px;
}
